const analytics = new AnalyticsEvents( gtag );

//BOOKING START FORM - GET EVENT AND PRODUCT DETAILS
if ( typeof(viewType) !== 'undefined' && typeof(wcProductId) !== 'undefined' && eventTypeId && typeof(inductionEventTypeId) !== 'undefined' ){

  if( viewType == 'booking-start'){

    //api
    const api = new Api();
    let base_url = api.base_url;
    let key = api.api;
    let limit = api.limit;

    //set event type and product objects
    let eventType = new EventType( eventTypeId );
    let product = null;
    eventType.getEventTypeById( eventTypeId, api )
      .then( eventType => {
        //get online product details
        product = new Product( eventType.onlineProductId );
        return product.getProductById( eventType.onlineProductId, api )
      })
      .then( result => {
        console.log(result);
      })

    //get/set induction details and edit dom on booking start form
    let inductionEventType = new EventType( inductionEventTypeId );
    let inductionProduct = null;
    inductionEventType.getEventTypeById( inductionEventTypeId, api )
      .then( eventType => {
        // console.log(eventType);
        //get online product details
        inductionProduct = new Product( eventType.onlineProductId );
        return inductionProduct.getProductById( eventType.onlineProductId, api )
      })
      .then( result => {
        // console.log(inductionProduct);
        //update dom
        let inductionCostPlaceholdersEls = document.getElementsByClassName('induction_cost');
        let inductionCostPlaceholders = Array.from(inductionCostPlaceholdersEls);
        inductionCostPlaceholders.forEach( function( element ) {
          element.innerHTML = '&pound;' + parseInt(inductionProduct.price1, 10).toFixed(2);
        })
      })
  }
}

//SHOW EXISTING EVENTS
if (typeof(wcProductId) !== 'undefined' && typeof(qtyTotal) !== 'undefined' && typeof(eventTypeId) !== 'undefined' && (viewType == 'calendar' || viewType == 'day') ){

  //api
  const api = new Api();
  let base_url = api.base_url;
  let key = api.api;
  let limit = api.limit;

  //get product ids from variations and Other Product Ids var
  let otherProducts = [];
  if(productTimeVariations.length){
    productTimeVariations.forEach( function( timeVariation ){
      otherProducts.push( {
        id: timeVariation.product_id,
        wcProductId: timeVariation.wc_product_id,
        name: timeVariation.name,
        price: timeVariation.price,
        regularPrice: timeVariation.regular_price,
        onSale: timeVariation.on_sale,
        startTimes: timeVariation.product_start_times,
        includedPeriods: timeVariation.included_periods,
        other_product_ids: timeVariation.other_product_ids
      } );
    })
  }

  //induction info
  if( qtyInduction > 0 ){
    let inductionEventType = new EventType( inductionInfo.event_type_id );
    let inductionProduct = null;
    inductionEventType.getEventTypeById( inductionInfo.event_type_id, api )
      .then( eventType => {
        // console.log(eventType);
        //get online product details
        inductionProduct = new Product( eventType.onlineProductId );
        return inductionProduct.getProductById( eventType.onlineProductId, api )
      })
      .then( result => {
        inductionInfo.price = parseInt(inductionProduct.price1).toFixed(2);
      })
  }

  //CONSTRUCT EVENTS WRAPPER OBJECT AND ADD DOM ELEMENTS
  const EventsWrapper = new Events(wcProductId, wcEventTitle, qtyTotal, qtyCadets, qtyInduction, eventTypeId, siblingEventTypeIds, eventDayInfo, startDate, endDate, eventDuration, inductionInfo, currentUser, customerId, partyInfo, null, singleReservation, setTimesOnly, scheduleSettings, api, newReservations, myEvents, wcPrice, wcRegularPrice, onSale);

  //view type day
  if(viewType=='day'){

    //MAIN TRACK
    if ( document.getElementById( 'schedule' ) ){
      //create schedule el
      let scheduleEl = document.getElementById( 'schedule' );
      EventsWrapper.schedule = new Schedule( scheduleEl, startDate, scheduleSettings.start, scheduleSettings.end, scheduleSettings.period, tracks, api );
      //add loading spinners to column loading containers
      EventsWrapper.schedule.startLoading( 0 );

      EventsWrapper.getEventTypes( tracks.main )
        .then( response => {
          return EventsWrapper.getProducts( productId, otherProducts )
        })
        .then( result => {
          EventsWrapper.product.wcName = wcEventTitle;
        //   return EventsWrapper.setProductVariations( productTimeVariations )
        // })
        // .then( result => {
          return EventsWrapper.constructEventReservations()
        })
        .then( result => {
          return EventsWrapper.getMyEventInfo();
        })
        .then( result => {
          //construct timeslots
          EventsWrapper.constructTimeslots();
          //fill timeslots
          EventsWrapper.fillTimeslots();
          //draw add buttons
          let totalAddButtons = 0;
          if( newReservations ){
            totalAddButtons = EventsWrapper.drawAddButtons();
          }
          //draw events
          let totalAvailableEvents = EventsWrapper.drawEvents();
          //if no availability
          if(totalAddButtons + totalAvailableEvents.available < 1){
            EventsWrapper.schedule.trackWrappers.mainTrack.classList.add('no-availability')
            //add text only if no hidden events
            // console.log(totalAvailableEvents);
            if( totalAvailableEvents.unavailable < 1 ){
              let noAvailabilityNotice = document.createElement('p');
              let noAvailabilityText = document.createTextNode('No availability on this date');
              noAvailabilityNotice.appendChild( noAvailabilityText );
              EventsWrapper.schedule.trackWrappers.mainTrack.appendChild( noAvailabilityNotice );
            }
          }
          //removeSpinner
          EventsWrapper.schedule.stopLoading( 0 );
          console.log(EventsWrapper);
          //call other tracks
          if( tracks.other_tracks.length > 0 ){
            EventsWrapper.schedule.renderOtherTracks( myEvents );
          }

          //load form automatically
          if (typeof eventReservationIdFromURL !== 'undefined') {
            if (eventReservationIdFromURL > 0) {
              console.log(eventReservationIdFromURL);
              EventsWrapper.showEventDetails({target: document.getElementById(eventReservationIdFromURL)}, null, eventReservationIdFromURL, isFamilyEvent);
            }
          }
          
        })
        .catch( err => {
          console.log( err );
          //catch failed to fetch err
          if( err.message.indexOf('Failed to fetch') > -1 ){
            //message box elements
            let messageBox = document.createElement('div');
            let messagePara = document.createElement('p');
            messagePara.innerHTML = 'We\'re having some problems communicating with our booking system, sorry! Please try again later, or call us on <a href="tel:01915214050">0191 521 4050</a>.';
            let homeLink = document.createElement('a');
            let homeLinkText = document.createTextNode('Return to homepage');
            homeLink.href = '/';
            homeLink.appendChild( homeLinkText );
            messageBox.appendChild( messagePara );
            messageBox.appendChild( homeLink );
            showOverlay( true, false, messageBox )
          }
        })
    }

  } else {
    //month or other view type
    //fetch data from api
    EventsWrapper.getEventTypes( tracks.main )
      .then( response => {
        return EventsWrapper.getProducts( productId, otherProducts )
      })
      // .then( result => {
      //   return EventsWrapper.setProductVariations( productTimeVariations )
      // })
      .then( result => {
        return EventsWrapper.constructEventReservations()
      })
      .then( result => {
        //construct timeslots
        EventsWrapper.constructTimeslots();
        //fill timeslots
        EventsWrapper.fillTimeslots();
        //check availability
        EventsWrapper.checkAvailability();
        console.log(EventsWrapper);
      })
      .catch( err => {
        console.log( err );
      })

    //target dom elements for aanlytics events
    if( document.getElementsByClassName('calendar').length > 0 ){
      let dayEls = document.getElementsByClassName('day');
      [...dayEls].forEach( dayEl => {
        let date = dayEl.getAttribute('data-date');
        [...dayEl.children].forEach( child => {
          if( child.tagName == 'A' ){
            let href = child.href;
            child.addEventListener('click', (e) => {
              e.preventDefault();
              analytics.calendarDateChosen( date )
                .then( result => {
                  window.location.href = href;
                })
            });
          }
        })
      })
    }
  }
}
