import { scrollTo } from './scrollTo.js';
import AnalyticsEvents from '../analytics/analyticsEvents.js';

const analytics = new AnalyticsEvents( gtag );

let eventCategoryInputEls = document.getElementsByName('event_category');
let eventCategoryInputs = Array.from( eventCategoryInputEls );
eventCategoryInputs.forEach( function( input ){
  input.addEventListener('change', (e) => { showCategoryEvents(e.target.value) } );
  //check if already selected (when go back form is pre-filled)
  if (input.checked) {
    showCategoryEvents( input.value );
  }
});

function showCategoryEvents( trgtClass ){
  //show header
  document.getElementById('choose-event-header').style.display = 'inline-block';
  //hide and deselect all
  let productInputEls = document.getElementsByClassName( 'event' );
  let productInputs = Array.from( productInputEls );
  let bookingStartForm = document.getElementById('booking-choose-event');
  productInputs.forEach( function( input ){
    input.checked = false;
    input.style.display = 'none';
    let productId = parseInt( input.value, 10);
    let productName = input.getAttribute('data-product-name');
    input.addEventListener( 'change', (e) => {
      analytics.bookingStart( productId, productName )
        .then( result => {
          bookingStartForm.submit();
        });
    });
  });

  //show that category
  let category = trgtClass;
  let productCatInputEls = document.getElementsByClassName(category);
  let productCatInputs = Array.from( productCatInputEls );
  productCatInputs.forEach( function( input ){
    input.style.display = 'inline-flex';
  });

  //scroll to
  let eventsCont = document.getElementsByClassName('events')[0];
  scrollTo( eventsCont, 160);
}

//party booking form
if( document.getElementsByName('qty_extra').length > 0 ){

  let extrasCheck = function( e ){
    let max = parseInt(e.target.getAttribute('max'), 10);

    if(e.target.value >= max){
      [...qtyExtraEls].forEach( function( element ){
        console.log(element);
        element.disabled = false;
      })
    } else {
      [...qtyExtraEls].forEach( function( element ){
        console.log(element);
        element.disabled = true;
      })
    }
  }

  let qtyInput = document.getElementById('qty_party_guests');
  let qtyExtraEls = document.getElementsByClassName('party_extra_guests');

  qtyInput.addEventListener( 'change', extrasCheck );

}

//change numbers input
if( document.getElementById('qty_total') ){
  document.getElementById('qty_total').addEventListener( 'change', e => { onQtyTotalChange(e) } );
  document.getElementById('qty_total').addEventListener( 'change', e => { qtyValidations(e) } );
}

if( document.getElementById('qty_cadets') ){
  document.getElementById('qty_cadets').addEventListener( 'change', e => { qtyValidations(e) } );
}

if( document.getElementById('qty_induction') ){
  document.getElementById('qty_induction').addEventListener( 'change', e => { qtyValidations(e) } );
}

function onQtyTotalChange(e){
  
  let minNumberNewRes = e.target.getAttribute('data-min-new');
  let qtyTotalNotice = null;
  if( document.getElementsByClassName('qty-total-notice').length > 0 ){
    qtyTotalNotice = document.getElementsByClassName('qty-total-notice')[0];
    if( e.target.value < minNumberNewRes ){
      qtyTotalNotice.style.display = 'inline-block';
    } else {
      qtyTotalNotice.style.display = 'none';
    }
  }

}

function qtyValidations(e) {

  let totalQtyField = document.getElementById('qty_total');
  let totalQty = parseInt(totalQtyField.value);
  let cadetQtyField = document.getElementById('qty_cadets');
  let cadetQty = parseInt(cadetQtyField.value);
  let inductionQty = parseInt(document.getElementById('qty_induction').value); 
  let submitBtn = document.getElementById('booking-start-form-submit');

  let cadetValidationMsg = null;
  let inductionValidationMsg = null;

  // handle cadets
  if( document.getElementsByClassName('qty-cadet-validation').length > 0 ){
    cadetValidationMsg = document.getElementsByClassName('qty-cadet-validation')[0];
    if( totalQty < cadetQty ){
      cadetValidationMsg.style.display = 'inline-block';
      totalQtyField.style.borderColor = '#c01349';
      submitBtn.disabled = true;
    } else {
      cadetValidationMsg.style.display = 'none';
      totalQtyField.style.borderColor = '#575756';
      submitBtn.disabled = false;
    }
  }

  // handle inductions
  if( document.getElementsByClassName('qty-induction-validation').length > 0 ){
    inductionValidationMsg = document.getElementsByClassName('qty-induction-validation')[0];
    if( cadetQty < inductionQty ){
      inductionValidationMsg.style.display = 'inline-block';
      cadetQtyField.style.borderColor = '#c01349';
      submitBtn.disabled = true;
    } else {
      inductionValidationMsg.style.display = 'none';
      cadetQtyField.style.borderColor = '#575756';
      submitBtn.disabled = false;
    }
  }
}

//numbers chosen form submitted
if( document.getElementById('booking-start-form-submit') ){
  let bookingStartFormSubmit = document.getElementById('booking-start-form-submit');
  let bookingStartForm = bookingStartFormSubmit.parentNode;
  let formElements = bookingStartFormSubmit.parentNode.elements;
  let productId = parseInt(formElements.prod.value, 10);
  let totalParticipants = parseInt(formElements.qty_total.value, 10);
  bookingStartFormSubmit.addEventListener( 'click', (e) => {
    e.preventDefault();
    analytics.numbersChosen( totalParticipants, productId )
      .then( result => {
        bookingStartForm.submit();
      })
  } )
}
