import moment from 'moment';
import { createHeader, createParagraph } from '../utilities/elements.js';

function OtherTrack( trackId, trackInfo, targetEl ){

  this.trackId = trackId;
  this.trackInfo = trackInfo;
  this.targetEl = targetEl;

  this.eventTypes = [];
  this.eventReservations = {};
  this.timeslots = [];

}

OtherTrack.prototype.getEventTypes = function ( api ) {
  return new Promise( function( resolve, reject ){

    let eventTypesQueryString = api.base_url + 'eventTypes?key=' + api.api + '&limit=' + api.limit + '&where={"deleted":"false", "trackId":' + this.trackId + '}';

    fetch( eventTypesQueryString )
      .then( response => api.checkResponse(response) )
      .then( response => api.returnJson(response) )
      .then( eventTypes => {
        eventTypes.forEach( function( eventType ){
          this.eventTypes.push( eventType.eventTypeId )
        }.bind(this))
        resolve( eventTypes )
      })
      .catch( err => {
        reject( err );
      })

  }.bind(this))
};

OtherTrack.prototype.getEventReservations = function ( startDate, endDate, api ) {
  return new Promise(function(resolve, reject) {

    let eventResQueryString = api.base_url + 'eventReservations?key=' + api.api + '&limit=' + api.limit +
    '&where={"status":{"$in":[0,1,2,3,6]}, "deleted":"false", "startTime": {"$gte":"' + startDate + 'T00:00:00", "$lte":"' + endDate + 'T23:59:00"}, "eventTypeId":{"in":[' + this.eventTypes + ']}}';

    fetch( eventResQueryString )
      .then( response => api.checkResponse(response) )
      .then( response => api.returnJson(response) )
      .then( eventReservations => {
        eventReservations.forEach( function( eventReservation ){
          this.eventReservations[eventReservation.eventReservationId] = eventReservation;
        }.bind(this))
        resolve( eventReservations )
      })
      .catch( err => {
        reject( err );
      })

  }.bind(this));
};

OtherTrack.prototype.drawEvents = function ( scheduleSettings, myEvents ) {

  //schedule
  let scheduleEl = this.targetEl;
  //fill for events
  if(this.eventReservations){
    let eventResKeys = Object.keys(this.eventReservations);
    for (const eventResKey of eventResKeys) {
      let eventRes = this.eventReservations[eventResKey];
      let startTime = moment(eventRes.startTime).format('kmm');
      let endTime = moment(eventRes.endTime).format('kmm');
      if( parseInt(startTime, 10) < parseInt(scheduleSettings.start, 10) && parseInt(endTime, 10) > parseInt(scheduleSettings.start, 10) ){
        //too early, but finishes after opening - change start to earliest of main schedule
        startTime =  scheduleSettings.start;
      }
      if( parseInt(endTime, 10) > parseInt(scheduleSettings.end, 10) && parseInt(startTime, 10) < parseInt(scheduleSettings.end, 10)){
        //too late, but starts before close - change start to earliest of main schedule
        endTime =  scheduleSettings.end;
      }

      //create element if within scheudle hours
      if ( parseInt( startTime, 10 ) >= parseInt( scheduleSettings.start, 10 ) && parseInt( endTime, 10 ) <= parseInt( scheduleSettings.end, 10 ) ) {
        let eventEl = document.createElement('article');
        this.addColumnChildClass( eventEl );
        eventEl.id = eventRes.eventReservationId;
        eventEl.classList.add('schedule-event');
        eventEl.style.cssText = 'grid-row: row' + startTime + ' / row' + endTime + '; grid-column: colEvent' + this.trackId + ' / span 1;';

        let myEventsKeys = Object.keys(myEvents).map( key => parseInt( key, 10 ) );
        if( myEventsKeys.indexOf( eventRes.eventReservationId ) > -1 ){
          let myEventEl = createParagraph( myEvents[eventRes.eventReservationId].title );
          let eventTitle = createHeader( 'h4', 'My Event' );
          let cartLink = document.createElement('a');
          cartLink.href = '/cart';
          cartLink.target = '_self';
          eventEl.appendChild( eventTitle );
          eventEl.appendChild( myEventEl );
          eventEl.appendChild( cartLink );
          eventEl.classList.add('my-event');
        } else {
          eventEl.classList.add('hidden');
        }

        scheduleEl.appendChild(eventEl);
      }
    }
  }
};

OtherTrack.prototype.drawSelectHeader = function ( tracks ) {
  //draws select elements for track headers - change event bound in bookings.js
  let selectEl = document.createElement('select');
  selectEl.setAttribute( 'data-current', this.trackId );
  this.addColumnChildClass( selectEl );
  selectEl.style.cssText = 'grid-row: 1 / span 1; grid-column: colEvent' + this.trackId + ' / span 1';
  //draw options
  for ( const track_index in tracks.track_info ){
    let track = tracks.track_info[track_index];
    //create array of shown tracks
    let shown_tracks = Array.from(tracks.other_tracks);
    shown_tracks.push( tracks.main );
    //if not main and not in other_tracks
    if ( shown_tracks.indexOf( parseInt(track.track_id, 10) ) < 0 || parseInt(track.track_id, 10) === this.trackId ) {
      let optionEl = document.createElement('option');
      let optionText = document.createTextNode(track.track_name);
      optionEl.value = track.track_id;
      //if current track select
      if( parseInt(track.track_id, 10) === this.trackId ){
        optionEl.selected = true;
      }
      optionEl.appendChild( optionText );
      selectEl.appendChild( optionEl );
    }
  }
  this.targetEl.appendChild( selectEl );
  return selectEl; //return for binding event
};

OtherTrack.prototype.drawClosed = function ( startDate, trackInfo, scheduleSettings ) {
  console.log(startDate);
  //schedule
  let scheduleEl = document.getElementById('schedule');
  let day = moment(startDate).format('dddd').toLowerCase();
  let open = trackInfo['opening_hours'][day]['open'].replace(':','');
  let close = trackInfo['opening_hours'][day]['close'].replace(':','');
  let closed = trackInfo['opening_hours'][day]['closed'];
  console.log(trackInfo, day, closed);
  //if closed
  if( closed ){
    let eventEl = document.createElement('article');
    this.addColumnChildClass( eventEl );
    eventEl.classList.add('schedule-event');
    eventEl.classList.add('closed');
    eventEl.style.cssText = 'grid-row: row' + parseInt(scheduleSettings.start, 10) + ' / row' + parseInt(scheduleSettings.end, 10) + '; grid-column: colEvent' + this.trackId + ' / span 1;';
    scheduleEl.appendChild(eventEl);
  } else {
    //open - if open later than main track start
    if( parseInt( open, 10 ) > parseInt( scheduleSettings.start, 10 ) ){
      let eventEl = document.createElement('article');
      this.addColumnChildClass( eventEl );
      eventEl.classList.add('schedule-event');
      eventEl.classList.add('closed');
      eventEl.style.cssText = 'grid-row: row' + parseInt(scheduleSettings.start, 10) + ' / row' + parseInt(open, 10) + '; grid-column: colEvent' + this.trackId + ' / span 1;';
      scheduleEl.appendChild(eventEl);
    }
    //close - if close earlier than main track end
    if( parseInt( close, 10 ) < parseInt( scheduleSettings.end, 10 ) ){
      let eventEl = document.createElement('article');
      this.addColumnChildClass( eventEl );
      eventEl.classList.add('schedule-event');
      eventEl.classList.add('closed');
      eventEl.style.cssText = 'grid-row: row' + parseInt(close, 10) + ' / row' + parseInt(scheduleSettings.end, 10) + '; grid-column: colEvent' + this.trackId + ' / span 1;';
      scheduleEl.appendChild(eventEl);
    }
  }
};

OtherTrack.prototype.addColumnChildClass = function ( targetEl ) {
  targetEl.classList.add('track-' + this.trackId + '-item');
}

export default OtherTrack;
