function AnalyticsEvents ( gtag ) {
  this.gtag = gtag;
}

AnalyticsEvents.prototype.bookingStart = function ( productId, productName ) {
  return new Promise( function( resolve, reject ){
    let result = this.gtag('event', 'Booking Start', {
      'event_category' : 'ecommerce',
      'event_label' : productName,
      'value' : productId
    });
    console.log('Booking start event triggered' + result);

    resolve(true);
  }.bind(this))
};

AnalyticsEvents.prototype.numbersChosen = function ( totalParticipants, productId ) {
  return new Promise( function( resolve, reject ){
    this.gtag('event', 'Numbers Chosen', {
      'event_category' : 'ecommerce',
      'event_label' : productId,
      'value' : totalParticipants
    });
    console.log('Numbers chosen event triggered');

    resolve(true);
  }.bind(this))
};

AnalyticsEvents.prototype.calendarDateChosen = function ( date ) {
  return new Promise( function( resolve, reject ){
    this.gtag('event', 'Calendar Date Chosen', {
      'event_category' : 'ecommerce',
      'event_label' : date
    });
    console.log('Calendar date event triggered');

    resolve(true);
  }.bind(this))
};

export default AnalyticsEvents;
