import Storage from '../utilities/storage.js';

function EventType ( eventTypeId, description, eventTypeName, onlineProductId, ptsPerReservation, trackId ) {

  this.eventTypeId = eventTypeId;
  this.description = description;
  this.eventTypeName = eventTypeName;
  this.onlineProductId = onlineProductId;
  this.ptsPerReservation = ptsPerReservation;
  this.trackId = trackId;

}

EventType.prototype.getEventTypeById = function ( eventTypeId, api ) {

  //if no id decalred just use object id
  if(!eventTypeId){
    let eventTypeId = this.eventTypeId;
  }

  return new Promise( function( resolve, reject ){
    //check local storage
    let storage = new Storage();
    let eventTypeStorage = storage.retrieve( 'eventType' );
    if ( eventTypeStorage ) {
      //check for id in storage
      if ( eventTypeStorage.items[eventTypeId] ){
        //if exists - check hasn't expired
        if( !storage.expired( eventTypeStorage.items[eventTypeId] ) ){
          //set attributes
          let result = eventTypeStorage.items[eventTypeId];
          this.description = result.description;
          this.eventTypeName = result.eventTypeName;
          this.onlineProductId = result.onlineProductId;
          this.trackId = result.trackId;
          //resolve if meets these criteria
          console.log(' event type fetched from storage');
          return resolve( eventTypeStorage.items[eventTypeId] )
        }
      }
    }
    //fetch if requried
    this.getEventTypeFromApi( api )
      .then( eventType => {
        console.log('from api');
        storage.store( eventType.eventTypeId, 'eventType', eventType );
        resolve(eventType)
      })

  }.bind(this))
}

EventType.prototype.getEventTypeFromApi = function ( api ) {
  return new Promise( function( resolve, reject ){

    let eventTypesQueryString = api.base_url + 'eventTypes/' + this.eventTypeId + '?key=' + api.api;
    fetch( eventTypesQueryString )
      .then( response => api.checkResponse(response) )
      .then( response => api.returnJson(response) )
      .then( result => {
        this.description = result.description;
        this.eventTypeName = result.eventTypeName;
        this.onlineProductId = result.onlineProductId;
        this.trackId = result.trackId;
        resolve( result )
      })
      .catch( err => {
        reject( err );
      })

  }.bind(this))
}


export default EventType;
