import OtherTrack from './scheduleOtherTracks.js';
import moment from 'moment';
import { addSpinner, removeSpinner } from '../utilities/spinner.js'; //ui js

function Schedule ( targetEl = null, date, start = 800, end = 2200, period = 30, tracks, api ){

  this.targetEl = targetEl;
  this.date = date;
  this.start = start;
  this.end = end;
  this.period = period;
  this.tracks = tracks;
  this.api = api;

  this.trackWrappers = {};
  this.colWidth = 'minmax(14rem, 24rem)';

}

Schedule.prototype.modifyStartTime = function ( eventStartTime ) {
  this.start = eventStartTime;
  this.modifyRowTemplate();
};

Schedule.prototype.modifyEndTime = function ( eventEndTime ) {
  this.end = eventEndTime;
  this.modifyRowTemplate();
};

Schedule.prototype.modifyRowTemplate = function () {
  let startMoment = moment( this.start, 'Hmm');
  let endMoment = moment( this.end, 'Hmm');
  let thisMoment = startMoment.clone();
  let templateString = '[trackname] minmax(2rem, 1fr)';
  while( thisMoment <= endMoment ){
    //modify template string
    let time = thisMoment.format('kmm');
    let timeDisplay = thisMoment.format('H:mm');
    templateString += ' [row'+time+'] minmax(2rem, 1fr)';
    //append time element if doesn't exist
    if( document.getElementsByClassName( time ).length < 1 ){
      let timeElCont = document.createElement('div');
      let timeEl = document.createElement('h4');
      let timeText = document.createTextNode( timeDisplay );
      timeElCont.classList.add('time');
      //set hour class
      if( thisMoment.format('m') === 0 ){
        timeElCont.classList.add('hour');
      }
      //grid row
      timeElCont.style.gridRow = 'row' + time + ' / span 1';
      timeEl.appendChild( timeText );
      timeElCont.appendChild( timeEl )
      this.targetEl.appendChild( timeElCont );
    }
    thisMoment.add( this.period, 'm' );
  }
  this.targetEl.style.gridTemplateRows = templateString;
};

Schedule.prototype.renderOtherTracks = function ( myEvents ) {
  return new Promise( function( resolve, reject ){

    this.setGridColumnsTemplate();

    this.tracks.other_tracks.forEach( function( trackId ){
      //create object
      let track = new OtherTrack( trackId, this.tracks.track_info[trackId], this.targetEl );
      //start loading
      this.startLoading( trackId );

      track.getEventTypes( this.api )
        .then( eventTypes => {
          return track.getEventReservations( this.date, this.date, this.api )
        })
        .then( eventReservations => {
          let selectHeader = track.drawSelectHeader( this.tracks );
          //bind loading to select header
          selectHeader.addEventListener( 'change', this.changeTrack.bind(this) );
          track.drawEvents( scheduleSettings, myEvents );
          console.log(this);
          track.drawClosed( this.date, this.tracks.track_info[trackId], scheduleSettings);
          this.stopLoading( trackId );
        })
        .catch( err => {
          console.error( err );
        })

    }.bind(this))

  }.bind(this))
};

Schedule.prototype.changeTrack = function (e) {
  let selectEl = e.target;
  let currentTrack = parseInt(selectEl.getAttribute( 'data-current' ), 10);
  let newTrackId = parseInt(e.target.value, 10);
  selectEl.setAttribute('data-current', newTrackId);
  //replace in array
  let currIndex = this.tracks.other_tracks.indexOf( currentTrack );
  this.tracks.other_tracks[ currIndex ] = newTrackId;
  //remove old children
  let currentTrackChildren = document.getElementsByClassName('track-' + currentTrack + '-item');
  [...currentTrackChildren].forEach( function( childEl ){
    childEl.remove();
  })
  //call render again
  this.renderOtherTracks();
}

Schedule.prototype.setGridColumnsTemplate = function () {
  let gridTemplateColsString = '[times] 3.5rem [main] ' + this.colWidth + ' ';
  //loop through other tracks
  this.tracks.other_tracks.forEach( function( trackId ){
    gridTemplateColsString += '[colEvent' + trackId + '] ' + this.colWidth + ' ';
  }.bind(this))
  this.targetEl.style.gridTemplateColumns = gridTemplateColsString;
}

Schedule.prototype.startLoading = function ( trackId ) {
  if ( trackId == 0 ){
    //main track
    let trackWrapper = this.drawTrackWrapper(0);
    addSpinner( trackWrapper );
  } else {
    let trackWrapper = this.drawTrackWrapper( trackId );
    addSpinner( trackWrapper );
  }
};

Schedule.prototype.stopLoading = function ( trackId ) {
  if ( trackId == 0 ){
    //main
    let mainTrackWrapper = document.getElementsByClassName('main-track')[0];
    removeSpinner( mainTrackWrapper );
  } else {
    let trackWrapper = document.getElementsByClassName('track-' + trackId)[0];
    removeSpinner( trackWrapper );
  }
};

Schedule.prototype.drawTrackWrapper = function ( trackId ) {
  let trackWrapperEl = document.createElement('div');
  trackWrapperEl.classList.add('track-wrapper');
  //if trackId = 0 main track
  if( trackId == 0){
    trackWrapperEl.style.cssText = 'grid-column: main / span 1; grid-row: 2 / -1';
    trackWrapperEl.classList.add('main-track');
    this.trackWrappers.mainTrack = trackWrapperEl;
  } else {
    trackWrapperEl.style.cssText = 'grid-column: colEvent' + trackId + ' / span 1; grid-row: 2 / -1';
    trackWrapperEl.classList.add('track-' + trackId);
    trackWrapperEl.classList.add('track-' + trackId + '-item');
    this.trackWrappers['track-' + trackId] = trackWrapperEl;
  }

  this.targetEl.appendChild( trackWrapperEl );
  return trackWrapperEl;
};



export default Schedule;
