import moment from 'moment';

function Customer ( customerId, email, firstName, lastName, telephone ) {

  this.customerId = customerId;
  this.email = email;
  this.firstName = firstName;
  this.lastName = lastName;
  this.telephone = telephone || null;

}

Customer.prototype.checkCustomerExists = function (api) {
  return new Promise( function( resolve, reject ){

    let checkCustomerString = api.base_url + 'customers?key=' + api.api + '&select=customerId&where={"email":"' + this.email + '"}';

    fetch( checkCustomerString )
      .then( response => {
        if( !response.ok ){
          throw Error(response.statusText);
        }
        return response;
      })
      .then( response =>
        response.json()
      )
      .then( customers => {
        if (customers.length) {
          resolve(customers[0].customerId) //should only ever be 1 returned!
        } else {
          resolve(false)
        }
      })
      .catch( err => {
        reject(err);
      })

  }.bind(this))
}

Customer.prototype.createCustomerOnApi = function (api) {
  return new Promise( function( resolve, reject) {

    let now = moment().format('YYYY-MM-DDTHH:mm:ss');
    let postCustomerString = api.base_url + 'customers?key=' + api.api +
      '&accountCreated=' + now +
      '&email=' + this.email +
      '&firstname=' + this.firstName +
      '&lastname=' + this.lastName +
      '&phoneNumber=' + this.telephone;

    fetch( postCustomerString, {method: 'POST'})
      .then( response => {
        if( !response.ok ){
          throw Error(response.statusText);
        }
        return response;
      })
      .then( response =>
        response.json()
      )
      .then( newCustomerId => {
        resolve(newCustomerId)
      })
      .catch( err => {
        reject(err);
      })

  }.bind(this))
}

Customer.prototype.setCustomer = function(api) {
  return new Promise( function (resolve, reject) {

    if(this.customerId){
      resolve(this.customerId);
    } else {
      //check user does not exist
      this.checkCustomerExists(api)
        .then( customerId => {
          if(customerId){
            return customerId;
          } else{
            return this.createCustomerOnApi(api)
          }
        })
        .then( customerId => {
          this.customerId = customerId;
          resolve(customerId)
        })
    }
  }.bind(this))
}


export default Customer;
